body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App,
.App * {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.App {
  text-align: center;
  width: 100%;
  max-width: 780px;
  margin: auto;
  padding: 15px;
  color: #222;
  font: normal normal normal 1rem/1.6 Nunito Sans, Helvetica, Arial, sans-serif;
}

.slideshow,
.slideshow * {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.slideshow {
  width: 100%;
}

.slideshow > .container {
  height: 500px;
  position: relative;
  margin: auto;
  width: 100%;
}

.slideshow > .container > .slide {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: auto;
  opacity: 0;
  -webkit-transition: opacity 0.6s ease;
  transition: opacity 0.6s ease;
}

.slideshow > .container > .slide.active {
  opacity: 1;
}

.slideshow > .container > .slide > .slide__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slideshow > .container > .slide > .slide__number {
  color: #fff;
  font-size: 1rem;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.slideshow > .container > .slide > .slide__caption {
  color: #fff;
  font-size: 1.3rem;
  padding: 8px 12px;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.2);
}

.slideshow > .container > .prev,
.slideshow > .container > .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  font-size: 18px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  font-weight: bold;
  transition: background-color 0.6s ease;
  -webkit-transition: background-color 0.6s ease;
  -moz-transition: background-color 0.6s ease;
  -o-transition: background-color 0.6s ease;
}

.slideshow:hover > .container > .prev,
.slideshow:hover > .container > .next {
  background-color: rgba(0, 0, 0, 0.2);
}

.slideshow:hover > .container > .prev:hover,
.slideshow:hover > .container > .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.slideshow > .container > .next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.slideshow > .container > .prev {
  left: 0;
  border-radius: 0 3px 3px 0;
}

.slideshow > .dot-container {
  text-align: center;
  margin: 1rem auto;
}

.slideshow > .dot-container > .dot {
  height: 15px;
  width: 15px;
  margin: 0 0.4rem;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  -webkit-transition: background-color 0.6s ease;
  -moz-transition: background-color 0.6s ease;
  -o-transition: background-color 0.6s ease;
}

.slideshow > .dot-container > .dot.active,
.slideshow > .dot-container > .dot:hover {
  background-color: #717171;
}

.slideshow > .dot-container > .dot:not(.active):hover {
  cursor: pointer;
}

.image-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.nav__image {
  max-width: 100%;
  object-fit: cover;
  cursor: pointer;
  height: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0.5;
}

.nav__image.active {
  opacity: 1;
}

